import React, { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../store/store';
import { setProducts, filterFavorites } from '../store/productSlice';
import Card from '../components/Card';
import { Button } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';

const Products: React.FC = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { filteredProducts, favoritesOnly } = useSelector((state: RootState) => state.products);

  useEffect(() => {
    //     API
    const fetchProducts = async () => {
      try {
        const response = await axios.get('https://fakestoreapi.com/products');
        const products = response.data.map((item: any) => ({
          id: item.id.toString(),
          title: item.title,
          description: item.description,
          image: item.image,
          isLiked: false,
        }));
        dispatch(setProducts(products));
      } catch (error) {
        console.error('  :', error);
      }
    };

    fetchProducts();
  }, [dispatch]);

  return (
    <div style={{ padding: '16px' }}>
      <Button variant="contained" color="primary" onClick={() => dispatch(filterFavorites())}>
        {favoritesOnly ? ' ' : ' '}
      </Button>
      <div style={{ display: 'grid', gridTemplateColumns: 'repeat(auto-fill, minmax(200px, 1fr))', gap: '16px', marginTop: '16px' }}>
        {filteredProducts.map((product) => (
          <Card key={product.id} product={product} onClick={() => navigate(`/products/${product.id}`)} />
        ))}
      </div>
    </div>
  );
};

export default Products;