import React from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { RootState } from '../store/store';
import { Button } from '@mui/material';

const ProductDetails: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const navigate = useNavigate();
  const product = useSelector((state: RootState) => state.products.products.find((p) => p.id === id));

  if (!product) {
    return <div>  </div>;
  }

  return (
    <div style={{ padding: '16px' }}>
      <Button variant="contained" onClick={() => navigate('/products')} style={{ marginBottom: '16px' }}>
          
      </Button>
      <div>
        <img src={product.image} alt={product.title} style={{ width: '100%', maxHeight: '400px', objectFit: 'contain' }} />
        <h1>{product.title}</h1>
        <p>{product.description}</p>
      </div>
    </div>
  );
};

export default ProductDetails;